@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Ubuntu+Mono&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500&family=Ubuntu+Mono&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  display: -webkit-flex;
  display: flex;
  height: 100vh;
  font-family: Poppins;
}

.StoriesBoard {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  padding-right: 5vh;
  width: 100%;
  margin-left: 5vh;
  padding-top: 5vh;
}

.StoriesRow {
  display: -webkit-flex;
  display: flex;
  width: 100%;
  -webkit-align-items: flex-end;
          align-items: flex-end;

  margin-bottom: 80px;
}

.StorieTitle {
  font-family: Ubuntu Mono;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 12px;
  width: 100%;
  margin-bottom: 12px;
}

.Storie {
  width: 300px;
  text-align: start;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.Title {
  font-size: 24px;
  font-weight: 500;
  font-family: "Poppins", sans-serif;
  color: #4f4f4f;
  line-height: 36px;
  margin-bottom: 28px;
}

.SubTitle {
  font-family: Montserrat;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;

  /* identical to box height */
  text-align: center;

  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  color: #a9a9a9;

  background-color: white;
  text-align: center;
}

.Menu{width:237px;height:100%;min-height:100;background-color:#fafbfd;padding:46px;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;text-align:left}.Menu .Title{font-family:"Poppins",sans-serif;display:-webkit-flex;display:flex;font-weight:600;font-size:13px;line-height:19px;margin-bottom:20vh}.Menu .TitleFirst{color:#f7542e}.Menu .Element{color:#9e9e9e;font-family:Noto Sans JP;font-weight:500;font-size:14px;line-height:20px;margin-top:30px;cursor:pointer}.Menu .Element.Active{color:#090f31}
.Input{width:200px;height:56px;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}input,textarea{font-family:Noto Sans JP;width:100%;position:relative;background-color:transparent;height:auto;padding:1em !important;margin:0em !important;box-sizing:border-box;font-size:14px;border-radius:8px;outline:none !important;border:none}.InputContainer{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center}.Multiline{display:inline-block !important;border:none}.EndIcon,.StartIcon{padding:.5em;display:-webkit-flex;display:flex}.md{height:56px}.sm{height:40px}.FullWidth{width:100%}.Label{font-size:12px;font-family:Noto Sans JP;font-weight:400}.Label.error{color:#d32f2f}.Label.error.focus{color:#d32f2f}.Label.error.hover{color:#333}.Label.default{color:#333}.Label.default.focus{color:#2962ff}.Label.default.hover{color:#333}.HelperText{font-size:10px;color:#828282}.HelperText.error{color:#d32f2f}.InputContainer.default{border-radius:8px;border:1px solid #828282}.InputContainer.default:focus,.InputContainer.default.focus{border:1px solid #2962ff !important}.InputContainer.default:hover,.InputContainer.default.hover{border:1px solid #333}.InputContainer.error{border-radius:8px;border:1px solid #d32f2f}.InputContainer.error:focus,.InputContainer.error.focus{border:1px solid #d32f2f !important}.InputContainer.error:hover,.InputContainer.error.hover{border:1px solid #333 !important}.InputContainer.disabled{background-color:#f2f2f2 !important;border:1px solid #e0e0e0 !important;color:#828282}
